import { CommonModule } from '@angular/common';
import { ChangeDetectionStrategy, Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Router, RouterModule } from '@angular/router';
import * as bootstrap from 'bootstrap'
import { Observable, Subject, takeUntil } from 'rxjs';
import { SnackbarService } from 'src/app/shared/components/snackbar/snackbar.service'
import { Reservation } from 'src/app/shared/models/reservation.model';
import { ReservationService } from 'src/app/shared/services/reservation.service';

@Component({
  selector: 'app-reservations',
  standalone: true,
  imports: [
    CommonModule,
    RouterModule
  ],
  templateUrl: './reservations.component.html',
  styleUrls: ['./reservations.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ReservationsComponent implements OnInit, OnDestroy{

  reservations!: Observable<Reservation[]>;
  destroy$: Subject<void> = new Subject();
  selectedReservation!: Reservation;

  constructor(
    private reservationService: ReservationService,
    private snackbarService: SnackbarService,
    private router: Router,
    private route: ActivatedRoute,
  ) {}

  protected confirmDelete(): void {
    alert("test");
    // please display a NgbModal to confirm which item you try to remove
  }

  ngOnInit(): void {
    this.reservations = this.reservationService.getAllReservations();
  }

  openConfirmationDialog(reservation: Reservation) {
    this.selectedReservation = reservation;
    let element = document.getElementById('confirmationModal');
    if (element !== null) {
      const myModal = new bootstrap.Modal(element, {});
      myModal.show();
    } else {
      console.error('Modal element not found');
    }
  }

  confirmAndDelete(confirm: boolean) {
    if(confirm) {
      // delete reservation 
      console.log("User confirmed deletion.", this.selectedReservation.id);
      this.reservationService
      .deleteReservation(this.selectedReservation.id)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: () => {
          this.snackbarService.showSuccess();
          this.router.navigateByUrl("reservations");
        },
      });
    } else {
      console.log("User cancelled the deletion.");
    }
  }


  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
